import {
  mapMutations,
  mapGetters
} from 'vuex';

export default {
  layout: 'admin',
  data() {
    return {
      table: {
        max: 10
      },
      page: 1,
      customToolbar: [["bold", "italic", "underline"], [{ list: "ordered" }, { list: "bullet" }], [{'align': ''}, {'align': 'center'}, {'align': 'right'}, {'align': 'justify'}]],
      titleDialog: 'Tambah Laporan',
      tambahDialog: false,
      search: '',
      isUpdate: false,
      btnLoader: false,
      form: {
        judul: "",
        deskripsi: "",
        evidences: [],
        tgl_mulai: "",
        tgl_selesai: "",
        pelaksana_kegiatan: '',
        tautan: '',
        kategori_kegiatan: "",
        sumber_pembiayaan: '',
        segmen_kegiatan: '',
        tempat_kegiatan: '',
        aktif: true,
        kandungan_pancasila: ''
      },
      searchDate: ['', ''],
      searchGoverment: ''
    }
  },
  mounted() {
    this.$store.dispatch('lapor/getAll', {
      showall: 1
    });

    this.$store.dispatch('setting/getAll');
    this.$store.dispatch('goverment/getPlains', {
      showall: 0
    });
  },
  methods: {
    searchData(){
      let start_date = '';
      let end_date = '';
      if(this.searchDate){
        start_date = this.searchDate[0]
        end_date = this.searchDate[1]
      }
      this.$store.dispatch('lapor/getAll', {
        search: this.search,
        start_date: start_date,
        end_date: end_date,
        goverment: this.searchGoverment
      });
    },
    detailLaporan(data){
      this.$store.commit('lapor/setLaporan', data)
      this.$router.push('/admin/lapor/detail')
    },
    edit(data) {
      let form = JSON.parse(JSON.stringify(data))
      this.tambahDialog = true
      this.titleDialog = 'Edit Laporan'
      this.isUpdate = true
      form.tgl_mulai =  this.$moment(form.tgl_mulai, 'DD-MM-YYYY hh:mm:ss').format('YYYY-MM-DDTHH:mm:ss'); 
      form.tgl_selesai = this.$moment(form.tgl_selesai, 'DD-MM-YYYY hh:mm:ss').format('YYYY-MM-DDTHH:mm:ss'); 
      // form.kandungan_pancasila = form.kandungan_pancasila.split(',')
      this.form = form
    },
    resetForm() {
      this.form = {
        judul: "",
        deskripsi: "",
        evidences: [],
        tgl_mulai: "",
        tgl_selesai: "",
        pelaksana_kegiatan: '',
        tautan: '',
        kategori_kegiatan: "",
        sumber_pembiayaan: '',
        segmen_kegiatan: '',
        tempat_kegiatan: '',
        aktif: true,
        kandungan_pancasila: ''
      }
    },
    handleCurrentChange(val) {
      this.page = val;
    },
    onSubmit(type = 'store') {
      this.btnLoader = true
      let url = "/lapor/store";
      if (type == 'update') {
        url = `/lapor/${this.form.id}/update`
      }
      let pancasila = '';
      if(Array.isArray(this.form.kandungan_pancasila)){
        this.form.kandungan_pancasila.forEach(e => {
            if(e !== ''){
              pancasila += pancasila == '' ? e : (',' + e)
            }
        });
      }
      this.form.kandungan_pancasila = pancasila !== '' ? pancasila : this.form.kandungan_pancasila;
      this.$axios.post(url, this.form).then(resp => {
        if (resp.data.success) {
          this.$notify.success({
            title: 'Success',
            message: `Berhasil ${type == 'store' ? 'Menambah' : 'Mengubah'} Laporan`
          })
          this.tambahDialog = false
          this.resetForm()
          this.$store.dispatch('lapor/getAll', {});
        }
      }).finally(() => {
        this.btnLoader = false
      }).catch(err => {
        let error = err.response.data.data
        if (error) {
          this.showErrorField(error)
        } else {
          this.$notify.error({
            title: 'Error',
            message: err.response.data.message
          })
        }
      })
    },
    deleteLaporan(id) {
      this.$swal({
        title: 'Perhatian!',
        text: "Apakah anda yakin ingin menghapus data ini?",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Ya',
        cancelButtonText: 'Batal'
      }).then((result) => {
        if (result.isConfirmed) {
          this.$axios.delete(`/lapor/${id}/delete`).then(resp => {
            if (resp.data.success) {
              this.$notify.success({
                title: 'Success',
                message: 'Berhasil Menghapus Data'
              })
              this.tambahDialog = false
              this.$store.dispatch('lapor/getAll', {
                defaultPage: true
              });
            }
          }).finally(() => {
            //
          }).catch(err => {
            this.$notify.error({
              title: 'Error',
              message: err.response.data.message
            })
          })
        }
      })
    },
  },
  computed: {
    ...mapGetters("lapor", [
      'getLapors',
      'getLoader'
    ]),
    ...mapGetters("setting", [
      'getSetting'
    ]),
    ...mapGetters("goverment", [
      'getGovermentPlains'
    ]),
  },
  watch: {
    getLapors(newValue, oldValue) {

    },
    getSetting(newValue, oldValue){
      console.log(newValue)
    },
    search(newValue, oldValue) {
      // this.$store.dispatch('lapor/getAll', {
      //   search: newValue
      // });
    },
    page(newValue, oldValue) {
      this.$store.commit('lapor/setPage', newValue)
      this.$store.dispatch('lapor/getAll', {});
    }
  },
}

